<template>
    <div class="margin-tb-10 margin-lr-20 layout-content preauth-page-container">
        <div class="margin-b-10">
            <apply-steps :active-step="1"></apply-steps>
        </div>
        <div class="cm-panel">
            <i-form :model="formData" ref="formData" :label-width="120" :rules="ruleValidate">
                <!--身份证正反面上传-->
                <h3>征信授权</h3>
                <div class="info-title-header margin-t-14 margin-b-24"><span class="info-title-title">身份证上传 <span
                        style='color: #999;font-weight:normal'>(必须先上传人像面)</span></span></div>
                <input id="fileInput" ref="fileInput" type="file" :style="`display:none;`" name="file"
                       @change="changeImage($event)"
                       accept="image/jpeg,image/jpg,image/png">
                <!--身份证上传容器-->
                <div class="upload-container" style="width1: 428px; margin: auto;text-align: center;">
                    <template v-for="(item,index) in uploadFile" v-if="index < 2">
                        <div class="mfile-cell-box">
                            <div class="mfile-cell-con">
                                <div class="mfile-cell">
                                    <!--上传占位-->
                                    <div class="mfile-img-con" :class="item.error? 'mfile-upload-error': ''"
                                         v-show="item.error || ((!item.loading && !item.remoteKey) && !item.bloburl)">
                                        <div v-show="!item.loading" class="mfile-file-add" @click="selectFile(index)">
                                            <div class="mfile-file-add-iconbox">
                                                <Icon custom="iconfont icon-add"></Icon>
                                            </div>
                                        </div>
                                        <div class="mfile-img-errortip" v-if="item.error">
                                            <Icon custom="iconfont icon-warn"
                                                  style="vertical-align: middle;"></Icon>
                                            <span>上传失败</span>
                                        </div>
                                    </div>
                                    <!--已上传显示图片-->
                                    <div class="mfile-img-con" :class="item.error? 'mfile-upload-error': ''"
                                         v-show="((item.loading || (item.loading || item.remoteKey) && item.bloburl) || (item.error && item.bloburl)) &&!item.error">
                                        <div class="mfile-img-con2" v-show="item.bloburl || item.remoteKey">
                                            <img :src="item.bloburl || item.remoteKey"/>
                                        </div>
                                        <div class="mfile-img-loading" v-show="item.loading">
                                            <Icon custom="iconfont icon-uploading"></Icon>
                                            {{item.pct}}
                                        </div>
                                        <div class="mfile-reset-con" v-show="!item.loading">
                                            <Row>
                                                <i-col span="12"><a href="javascript:;" @click="deleteFile(index)">
                                                    <Icon custom="iconfont icon-delete"></Icon>
                                                    删除</a></i-col>
                                                <i-col span="12"><a href="javascript:;" @click="previewFile(index)">
                                                    <Icon custom="iconfont icon-search"></Icon>
                                                    查看大图</a></i-col>
                                            </Row>

                                        </div>
                                        <div class="mfile-img-errortip" v-if="item.error">
                                            <Icon type="ios-information-outline" size="16"></Icon>
                                            <span>上传失败</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="mfile-cell-title">{{item.title}}</div>
                        </div>
                    </template>
                </div>
                <!--身份证识别结果-->
                <div class="margin-t-10">
                    <Row class="">
                        <i-col :xs="24" :sm="12" :md="8">
                            <Form-item label="姓名" prop="name">
                                <i-input type="text" v-model="formData.name"
                                         :disabled="sfzyzSuccess || isSfzFileEmpty ||  allDisabled"
                                         placeholder="请输入">
                                </i-input>
                            </Form-item>
                        </i-col>
                        <i-col :xs="24" :sm="12" :md="8">
                            <Form-item label="身份证号" prop="certNo">
                                <i-input type="text" v-model="formData.certNo" disabled placeholder="请输入">
                                </i-input>
                            </Form-item>
                        </i-col>

                        <i-col :xs="24" :sm="12" :md="8">
                            <Form-item label="民族" prop="race">
                                <!-- <i-input type="text" v-model="formData.race" :disabled="sfzyzSuccess || isSfzFileEmpty ||allDisabled"
                                          placeholder="请输入"></i-input>-->
                                <i-select type="text" v-model="formData.race" name="race"
                                          :disabled="sfzyzSuccess || isSfzFileEmpty ||allDisabled"
                                          placeholder="请选择">
                                    <i-option v-for="(item,index) in dp['race']" :value="item.dictCode"
                                              :key="item.dictCode">
                                        {{item.dictName}}
                                    </i-option>
                                </i-select>
                            </Form-item>
                        </i-col>
                        <i-col :xs="24" :sm="12" :md="8">
                            <Form-item label="户籍地址" prop="address">
                                <i-input type="text" v-model="formData.address"
                                         :disabled="sfzyzSuccess ||isSfzFileEmpty || allDisabled"
                                         placeholder="请输入"></i-input>
                            </Form-item>
                        </i-col>
                        <i-col :xs="24" :sm="12" :md="8">
                            <Form-item label="有效期限" prop="validDate">
                                <i-input type="text" v-model="formData.validDate" disabled placeholder="请输入"></i-input>
                            </Form-item>
                        </i-col>
                        <i-col :xs="24" :sm="12" :md="8">
                            <Form-item>
                                <i-button type="primary" custom-icon="iconfont icon-verify"
                                          :disabled="!sfzCompleted || formData.sfzres=='1' || allDisabled"
                                          @click="checkCredit">验证
                                </i-button>
                            </Form-item>
                        </i-col>
                    </Row>
                    <!--<Row>-->
                    <!--<div class="text-center">-->
                    <!--<i-button type="primary" custom-icon="iconfont icon-verify"-->
                    <!--:disabled="!sfzCompleted || formData.sfzres=='1' || allDisabled"-->
                    <!--@click="checkCredit">验证-->
                    <!--</i-button>-->
                    <!--</div>-->
                    <!--</Row>-->
                </div>
            </i-form>

            <!--银联鉴权-->
            <i-form :model="formBankData" ref="formBankData" :label-width="120" :rules="bankRuleValidate">
                <div v-if="formData.sfzres=='1'">
                    <div class="info-title-header margin-b-14" style="margin-top: 16px;"><span class="info-title-title">银联鉴权</span>
                    </div>
                    <FormItem label="请选择鉴权方式">
                        <RadioGroup v-model="formBankData.credit_type" :disabled="allDisabled">
                            <Radio v-for="item in dp['creditType']" :label="item.value"
                                   :disabled="allDisabled || (socklinkError && item.value=='1')" :key="item.value">
                                <span>{{item.name}}</span></Radio>
                        </RadioGroup>
                    </FormItem>
                    <div v-if="formBankData.credit_type!='2'" class="centerdiv">
                        <div class="step-badge-header">
                            <span class="step-badge-num">1</span>
                            <span class="step-badge-title">步骤一</span>
                            <span class="step-badge-text">人脸识别</span>
                            <span class="step-badge-status" :class="weixinResClass">{{weixinResText}}</span>
                        </div>
                        <!--人脸识别-->
                        <div class="codeimg-con">
                            <img :src="qrCode"/>
                        </div>
                        <p class="text-center">推荐客户微信扫一扫，人脸识别快速完成授权</p>
                    </div>
                    <div class="centerdiv" v-if="formBankData.credit_type=='1' && formBankData.weixinRes">
                        <div class="step-badge-header">
                            <span class="step-badge-num">2</span>
                            <span class="step-badge-title">步骤二</span>
                            <span class="step-badge-text">银行卡验证</span>
                            <!--<span class="step-badge-status" :class="weixinResClass">失败</span>-->
                        </div>
                    </div>
                    <div v-if="formBankData.credit_type=='2' || (formBankData.credit_type=='1' && formBankData.weixinRes)"
                         class="centerdiv">
                        <Row>
                            <i-col :xs="24" :sm="16" :md="20">
                                <Form-item label="姓名" :label-width="190">
                                    <i-input type="text" v-model="formData.name" disabled placeholder="未填写">
                                    </i-input>
                                </Form-item>
                            </i-col>
                            <i-col :xs="24" :sm="16" :md="20">
                                <Form-item label="身份证号" :label-width="190">
                                    <i-input type="text" v-model="formData.certNo" disabled placeholder="未填写">
                                    </i-input>
                                </Form-item>
                            </i-col>
                        </Row>
                        <!--上传银行卡-->
                        <Row>
                            <i-col :xs="24" :sm="16" :md="20">
                                <Form-item label="银行卡号" prop="bankcardNo" :label-width="190">
                                    <i-input type="text" v-model="formBankData.bankcardNo" :disabled="allDisabled"
                                             placeholder="请输入">
                                    </i-input>
                                </Form-item>
                            </i-col>
                            <i-col :xs="24" :sm="6" :md="4">
                                <div style="margin-top:1px;margin-left:8px;">
                                    <i-button class="color-primary font12" type="text" :disabled="allDisabled"
                                              custom-icon="iconfont icon-scan"
                                              @click="selectFile(2)">
                                        识别银行卡照片
                                    </i-button>
                                </div>
                            </i-col>
                        </Row>
                        <Row>
                            <i-col :xs="24" :sm="16" :md="20">
                                <Form-item label="手机号" prop="phone" :label-width="190">
                                    <i-input type="text" v-model="formBankData.phone" :disabled="allDisabled"
                                             placeholder="请输入">
                                    </i-input>
                                </Form-item>
                            </i-col>
                            <i-col :xs="24" :sm="6" :md="4" v-if="formBankData.credit_type=='2'">
                                <div style="margin-top:1px;margin-left:8px;">
                                    <i-button class="color-primary font12" type="text" @click="submitCredit"
                                              custom-icon="iconfont icon-send"
                                              :disabled="sendSmsDisabled || allDisabled">{{sendSmsText}}
                                    </i-button>
                                </div>
                            </i-col>
                        </Row>
                    </div>
                </div>
                <div class="text-center font0" style="margin-top:26px;margin-bottom:16px;">
                    <i-button @click="cancelEdit" size="large">返回列表</i-button>
                    <i-button type="primary" :disabled="preauthBtnDisabled || allDisabled" @click="submitCredit"
                              size="large" v-if="formBankData.credit_type=='1'">发起预审
                    </i-button>
                </div>
            </i-form>
        </div>
        <!-- 短信授权验证：发送短信成功的弹窗 -->
        <Modal title="授权信息" v-model="showMiniResModal" :mask-closable="false" :closable="false">
            <div style="margin-top:-10px;">
                <div class="font18">已发送短信至：<span class="color-primary">{{formBankData.phone|mobile}}</span></div>
                <div>请提醒用户尽快完成授权！返回订单列表后可查看授权进度~</div>
            </div>
            <div slot="footer">
                <i-button type="primary" @click="closeMiniResModal">确 认</i-button>
            </div>
        </Modal>
        <!-- 短信授权验证后手机号未实名时：发送短信成功的弹窗 -->
        <Modal title="提示信息" v-model="showFailMiniResModal" :mask-closable="false" :closable="false">
            <div style="margin-top:-10px;">
                <div>手机号未实名，您可以返回修改手机号或者采用发送短信至<br><span class="color-primary">{{formBankData.phone|mobile}}</span>，进行人脸验证的方式。
                </div>
            </div>
            <div slot="footer">
                <i-button @click="showFailMiniResModal=false">返回修改</i-button>
                <i-button type="primary" @click="sendFaceSms">短信人脸</i-button>
            </div>
        </Modal>
        <!-- 查看大图的弹窗 -->
        <Modal v-model="showPreviewModal" class-name="vertical-center-modal"
               title="图片预览" width="60" footer-hide>
            <div class="text-center" style="width:100%;height:400px;">
                <img :src="previewUrl" alt="图片预览地址" style="max-width:100%;max-height:100%">
            </div>
        </Modal>
        <div class="simple-breadcrumb col-xs-11">
            <router-link to="/order/list">进件管理</router-link>
            <Icon type="ios-arrow-forward"></Icon>
            <span>征信授权</span>
        </div>
    </div>
</template>

<script>
    let wxStompClient;
    import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'

    import SockJS from 'sockjs-client';
    import Stomp from 'stompjs';
    import applySteps from "@/components/apply/applySteps.vue";
    import COSFN from "@/utils/cos";
    import { yx } from "@/utils/helper";
    import { validateRules, bankCardCheck } from '@/utils/validateRules';

    export default {
        name: "orderCredit",
        components: {
            applySteps,
        },
        data () {
            return {
                dp: {},
                account: this.$store.state.app.userInfo.userAccount,
                userName: this.$store.state.app.userInfo.userName,
                websocketUrl: this.$store.state.app.userInfo.websocketUrl,
                title: '征信授权',
                ruleValidate: validateRules.preauthPage, //表格校验规则
                bankRuleValidate: validateRules.preauthPage, //表格校验规则
                showPreviewModal: false, // 显示图片预览弹窗
                previewUrl: '',//图片预览大图地址
                dateGroup: [],
                uploadFile: [{
                    "itemID": "applicantOrderIdPositive",
                    title: '上传身份证人像面',
                }, {
                    "itemID": "applicantOrderIdReverse",
                    title: '上传身份证国徽面',
                }, {
                    "itemID": "creditCardPositive",
                    title: '识别银行卡照片',
                }],
                formBankData: {
                    "validDate": "",
                    "bankcardNo": "",
                    "phone": "",
                    "credit_type": "1", //鉴权方式默认为人脸
                    "weixinRes": undefined, //人脸识别的结果
                },
                formData: {
                    orderId: "", //已授权未过甚时100303，会返回订单号
                    "requestId": "",
                    "customerType": "1",
                    "name": "",
                    "certNo": "",
                    "race": "",
                    "address": "",
                    "validDate": "",
                    "validStartDate": "",
                    "validEndDate": "",
                    sfzres: '', //提交身份证信息验证后的结果类型
                },
                allDisabled: false, //整页禁用
                showMiniResModal: false, //显示返回结果弹窗
                showFailMiniResModal: false, //显示手机号未实名时的结果弹窗
                qrCode: '', //选择人脸识别提交后的二维码地址
                loading: false, //发送ajax请求时加载中，尚没对应弹窗
                sendSmsText: '发送短信', //发送短信按钮
                Cos: undefined, //创建Cos
                socklinkError: false,//socket连接错误
            }
        },

        computed: {
            //身份证信息识别完成状态
            sfzCompleted () {
                let sfzFileValid = (this.uploadFile[0].remoteKey && this.uploadFile[1].remoteKey) != '';
                let res = (sfzFileValid && this.formData.name && this.formData.certNo && this.formData.race && this.formData.address &&
                    this.formData.validDate) != '';
                return res;
            },
            //正身份证没有上传
            isSfzFileEmpty () {
                return this.uploadFile[0].remoteKey == '' || this.uploadFile[0].error;
            },
            //背身份证没有上传
            isSfzFileEmpty_back () {
                return this.uploadFile[1].remoteKey == '' || this.uploadFile[1].error;
            },
            //身份验证是否成功
            sfzyzSuccess () {
                return this.formData.sfzres == '1';
            },
            //发送短信按钮是否禁用
            sendSmsDisabled () {
                let phonevalid = /^1[3|4|5|6|7|8|9]\d{9}$/.test(this.formBankData.phone);
                let bankcardvalid = bankCardCheck(this.formBankData.bankcardNo);
                return !phonevalid || !bankcardvalid || this.allDisabled;
            },
            //预审按钮是否禁用
            preauthBtnDisabled () {
                return !this.sfzyzSuccess || !this.formBankData.weixinRes || this.sendSmsDisabled;
            },
            //人脸识别结果文字的样式
            weixinResClass () {
                const res = this.formBankData.weixinRes;
                return res === true ? 'step-badge-status-success' : res === false ? 'step-badge-status-error' : ''
            },
            //人脸识别结果文字
            weixinResText () {
                const res = this.formBankData.weixinRes;
                return res === true ? '通过' : res === false ? '失败' : '未识别'
            },
        },
        created () {
        },
        mounted () {
            this.queryDictData(['race', 'creditType']).then(dp => {
                this.dp = dp;
            })
            document.title = '进件提报-征信授权';
            //初始化文件格式
            this.uploadFile.forEach((item, index) => {
                this.initUploadFile(index);
            })
            //初始化查询
            this.queryInit();
        },
        destroyed () {
            if (wxStompClient) wxStompClient.disconnect();
        },
        methods: {
            ...mapMutations('app', ['showLoading', 'hideLoading']),
            ...mapActions('dict', ['queryDictData']),
            //清除表单信息
            fnReset (name) {
                this.$refs[name].resetFields();
            },
            //部分重置表单信息
            partialReset (name, propvalue) {
                this.$refs [name].fields.forEach(function (e) {
                    if (e.prop == propvalue) {
                        e.resetField()
                    }
                })
            },
            //人脸识别 socket连接
            connect () {
                // 建立连接对象（还未发起连接）
                var socket = new SockJS(this.websocketUrl + '?username=' + this.account);
                // 获取 STOMP 子协议的客户端对象
                wxStompClient = Stomp.over(socket);
                // 向服务器发起websocket连接并发送CONNECT帧
                wxStompClient.connect({}, (frame) => {
                    var destination = frame.headers['user-name'];
                    console.log('人脸识别已连接【' + frame + '】');
                    this.socklinkError = false;
                    wxStompClient.subscribe('/user/' + this.formData.requestId + destination + '/message', (response) => {
                        this.acceptMessage(response);
                    });
                }, (error) => {
                    console.log('人脸识别连接失败【' + error + '】');
                    this.socklinkError = true;
                    this.formBankData.credit_type = '2';
                    this.errorModal('人脸识别连接失败,请使用银行短信验证或刷新后重试!');
                });
            },
            //处理websocket接收到的消息
            acceptMessage (response) {
                let data = JSON.parse(response.body);
                let result = data.extendValue.result;
                if (data.messageType == "FACE_RECOGNITION") {
                    if (!result) {
                        this.formBankData.weixinRes = false;
                        this.errorModal(data.extendValue.errorMessage || '识别失败');
                    } else {
                        this.formBankData.weixinRes = true;
                    }
                }
            },
            //封装报错弹窗
            errorModal (content, onOk, okText) {
                let options = {
                    title: '提示',
                    content: content,
                    closable: false,
                    okText: okText || '确 定',
                    maskClosable: false,
                };
                if (onOk) options.onOk = onOk;
                this.$Modal.error(options);
            },
            //初始化上传文件通用
            initUploadFile (index) {
                let item = this.uploadFile[index];
                let arr = ["qcloudUrl", "bloburl", "name", "hash", "hashName", "remoteKey", "ext", "file", "size", "loading", "error"]
                arr.forEach(key => {
                    this.$set(item, key, "");
                })
            },
            // 查询未完成授权订单的征信信息，及二维码地址
            async queryInit () {
                //向后台请求requrestId，问题：二维码是否在此时返回，还是在身份验证成功后返回？
                this.showLoading();
                //有查询授权跟踪ID
                this.$http.post(polarisserver + '/credit/getTransactionId/T', {}).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    window.orderNo = result.data.orderNo
                    if (!result.success || !(result.data && result.data.orderNo)) return this.errorModal(result.errorMessage || '授权ID请求失败');
                    this.formData.requestId = result.data.orderNo; //表单中的跟踪ID更新
                    //带上参数创建COS
                    let params = {
                        "businessType": "credit",
                        "orderId": result.data.orderNo,
                        "viewCode": "yixin_xiaofei_tibao_polaris",
                        "orderUserId": this.account,
                        "orderUserName": this.userName
                    };
                    this.Cos = new COSFN(params);
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('授权ID请求出错：', err);
                })
            },
            //继续  被复用的已经做过征信授权的订单号
            getNewOrder (orderNo) {
                let obj = {
                    orderId: orderNo,//被复用的已经做过征信授权的订单号
                    sysId: '2',//系统标识app：1 ，pc：2
                    traceId: this.formData.requestId,//传输id
                };
                this.showLoading();
                this.$http.post(polarisserver + '/credit/createOrderForReuse', obj).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '复用已授权信息请求失败');
                    this.goToPreaudit(result.data.orderId);
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('复用已授权信息请求出错：', err);
                });
            },
            //带上订单号跳转预审页
            goToPreaudit (orderNo) {
                this.$router.replace({
                    name: "orderPreaudit",
                    query: {
                        orderNo: orderNo,
                    },
                    params: {
                        customerType: "1"
                    }
                });
            },
            //回到列表页
            backToList (orderNo) {
                this.$router.replace({
                    name: "orderList"
                });
            },
            //身份验证，将身份识别信息提交给后台进行验证，通过后再进行银联鉴权，前面的信息应该可以改
            checkCredit () {
                let formData = this.formData,
                    params = {
                        "traceId": formData.requestId,
                        "custName": formData.name,
                        "idCard": formData.certNo,
                        "sysId": "2", //系统标识。app：1 ； pc：2
                        "race": formData.race,
                    };
                this.$refs['formData'].validate((valid) => {
                    if (!valid)
                        return this.errorModal('表单填写未通过，请检查!');
                    this.showLoading();
                    this.$http.post(polarisserver + '/credit/checkIdCard', params).then((res) => {
                        this.hideLoading();
                        let result = res.data;
                        if (!result.success) return this.errorModal(result.errorMessage || '身份验证请求失败');
                        this.dealCheckCreditErrorCode(result)
                    }).catch((err) => {
                        this.hideLoading();
                        this.errorCatchModal('身份验证请求出错：', err);
                    })
                })
            },
            //身份验证成功后请求二维码
            getQrCode () {
                this.showLoading();
                let params = {
                    account: this.account,
                    custName: this.formData.name,
                    idCard: this.formData.certNo,
                    traceId: this.formData.requestId
                }
                this.$http.post(polarisserver + '/credit/getQrCode', params).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '二维码请求失败');
                    this.qrCode = 'data:image/jpeg;base64, ' + result.data.qrCode;
                    //建立socket连接
                    this.connect();
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('二维码请求出错：', err);
                });
            },
            //发起预审按钮、发送短信按钮、短信人脸按钮点击时的提交动作，传参type: 1：人脸+（3+3）,2:短信授权认证,3: 短信人脸
            todoSendCredit (type) {
                let { formData, formBankData } = this;
                //oauthStatus征信授权状态" 100606：人脸+（3+3）,100601:短信授权认证,100608 短信人脸
                //processType流程的类型 100901:表示人脸成功的流程,100903:短信链接的流程, 100907 短信人脸
                const oauthStatus = type == '1' ? '100606' : type == '2' ? '100601' : type == '3' ? '100608' : '';
                const processType = type == '1' ? '100901' : type == '2' ? '100903' : type == '3' ? '100907' : '';
                //短信人脸的请求地址不同
                const url = type == '3' ? polarisserver + '/credit/createOauthRecord' : polarisserver + '/credit/checkTelAndBankNo';
                let params = {
                    "custName": formData.name,
                    "idCard": formData.certNo,
                    "validStartDate": formData.validStartDate,
                    "validEndDate": formData.validEndDate,
                    "nation": formData.race,
                    "censusAddress": formData.address,
                    "custTel": formBankData.phone,
                    "bankCardNo": formBankData.bankcardNo,
                    oauthStatus,
                    processType,
                    "sysId": "2",
                    "traceId": formData.requestId,
                }
                this.showLoading();
                this.$http.post(url, params).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success)
                        return this.errorModal(type == '3' ? '短信发送失败，请重试！' : (result.errorMessage || '提交征信授权请求失败'));
                    //处理短信人脸结果
                    if (type == '3')
                        return this.dealOauthResult(result);
                    this.dealSubmitCreditErrorCode(result);
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('提交征信授权请求出错：', err);
                })
            },
            //发起预审、发送短信按钮：提交所有授权信息
            submitCredit () {
                this.$refs['formBankData'].validate((valid) => {
                    if (!valid) return this.errorModal('表单填写未通过，请检查!');
                    const type = this.formBankData.credit_type == '2' ? "2" : "1";
                    this.todoSendCredit(type);
                })
            },
            //处理短信人脸结果
            dealOauthResult (result) {
                const data = result.data;
                if (!data || !data.orderId) return this.errorModal('未生成订单号，请重试！');
                this.showMiniResModal = true;
            },
            //处理3+3验证结果
            dealSubmitCreditErrorCode (result) {
                if (!result || !result.data) return this.errorModal('征信授权请求返回数据为空');
                const data = result.data;
                switch (data.matchResult) {
                    // 100709 100712 手机号未实名 改为显示弹窗提示并可请求发送短信人脸
                    case "100709":
                    case "100712":
                        this.showFailMiniResModal = true;
                        break;
                    //100708 手机号与银行卡号均验证通过 ,其他为不匹配或无记录 //验证成功，人脸方式跳转预审页，短信授权方式显示弹窗
                    case "100708":
                        if (!data.orderId) return this.errorModal('未生成订单号，请重试！');
                        if (this.formBankData.credit_type == '2') this.showMiniResModal = true;
                        else this.goToPreaudit(data.orderId);
                        break;
                    default:
                        const resObj = {
                            '100706': '验证未通过，请修改“银行卡”',
                            '100707': '验证未通过，请修改“银行卡”',
                            '100710': '验证未通过，请修改“银行卡”和“手机号”',
                            '100711': '验证未通过，请修改“银行卡”和“手机号”',
                            '100713': '验证未通过，请修改“银行卡”和“手机号”',
                            '100714': '验证未通过，请修改“银行卡”和“手机号”',
                        }
                        //实际  100101   第三方接口返回错误信息
                        // let content = resObj[data.matchResult] || result.errorMessage || '验证未通过';
                        let content = result.errorMessage || '验证未通过';
                        this.errorModal(content);
                        break;
                }
            },
            //处理身份证二要素校验结果
            dealCheckCreditErrorCode (result) {
                const data = result.data;
                switch (data.matchResult) {
                    case "100201":
                        //100201:二要素匹配   继续操作下面的授权操作
                        this.formData.sfzres = '1';
                        this.getQrCode(); //身份识别结果后验证是否一致， 一致后可以请求二维码
                        break;
                    case "100203"://二要素数据库无记录, 弹窗提示“未查到该用户信息，请关闭本单！” 关闭弹窗，全部禁用，不返回列表
                        this.allDisabled = true;
                        return this.errorModal('未查到该用户信息，请关闭该单！', undefined, '关 闭');
                        break;
                    case "100204": //公安征信信息获取失败, 弹窗提示“校验失败，请尝试修改用户信息！“ 关闭弹窗，留在当前页
                        return this.errorModal('校验失败，请尝试修改用户信息！', undefined, '关 闭');
                        break;
                    case "100301":
                        //100301: 当前用户进入黑名单  关闭弹窗，留在当前页，全部禁用，不返回列表
                        this.allDisabled = true;
                        return this.errorModal('该用户为高危用户，请关闭该单！', undefined, '关 闭');
                        break;
                    case "100303":
                        //100303:已授权(未过期)，会返回订单号，继续按钮带上订单号云预审，放弃则关闭弹窗，留在当前页进行授权
                        this.formData.orderId = data.orderId;
                        return this.$Modal.confirm({
                            title: '提示',
                            content: '该用户已完成征信授权，建议您“继续"本单',
                            okText: '继 续',
                            onOk: () => {
                                this.getNewOrder(result.data.orderId);//有返回orderId
                            },
                            cancelText: '放 弃',
                            onCancel: () => {
                                //放弃时清空表单
                                this.clearCertFrontField();
                                this.clearCertBackField();
                                this.fnReset('formData');
                            }
                        });
                        break;
                    default:
                        //100202:二要素不匹配  或其他代码时，代表验证不成功，可继续修改
                        this.formData.sfzres = '';
                        this.errorModal('校验失败，尝试修改用户信息');
                        break;
                }
            },
            //关闭人脸识别授权成功后的结果弹窗，返回上一页
            closeMiniResModal () {
                this.showMiniResModal = false;
                this.$router.back();
            },
            //手机号未实名的结果弹窗中：点确定按钮请求后台，发送人脸的短信并返回列表页。点返回修改按钮可允许重新修改页面，即可修改银行卡号和手机号
            sendFaceSms () {
                this.showFailMiniResModal = false;
                this.todoSendCredit('3');
                return;
                this.showLoading();
                let { formData, formBankData } = this;
                let params = {
                    "custName": formData.name,
                    "idCard": formData.certNo,
                    "validStartDate": formData.validStartDate,
                    "validEndDate": formData.validEndDate,
                    "nation": formData.race,
                    "censusAddress": formData.address,
                    "custTel": formBankData.phone,
                    "bankCardNo": formBankData.bankcardNo,
                    //征信授权状态" 100601:短信授权认证,100606：人脸+（3+3）,100608 短信人脸
                    "oauthStatus": credit_type == '2' ? "100601" : "100606",
                    //流程的类型 100901:表示人脸成功的流程,100903:短信链接的流程, 100907 短信人脸
                    "processType": formBankData.credit_type == '2' ? "100903" : "100901",
                    "sysId": "2",
                    "traceId": formData.requestId,
                }
                //链接需要换
                this.$http.post(polarisserver + '/sms/sendShortMessage', params).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '发送短信人脸请求失败');
                    //成功之后返回列表页
                    // this.$router.back();
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('短信人脸请求出错：', err);
                })
            },
            //上传用到的
            selectFile (index) {
                if (this.allDisabled) return false;
                if (!this.Cos) {
                    return this.$Message.error('附件上传参数未获取成功，请稍后重新进入本页再试');
                }
                //选择文件事件，先定义是上传什么文件
                if (index == 1 && (!this.uploadFile[0].remoteKey || this.uploadFile[0].error)) {
                    return this.errorModal('请先上传身份证正面！');
                }
                this.inputIndex = index;
                this.$refs['fileInput'].value = ''; //先清空文件选择框的值
                this.$refs['fileInput'].click(); //输入框点击
            },
            //删除文件
            deleteFile (index) {
                if (this.allDisabled) return false;
                this.$Modal.confirm({
                    title: '提示',
                    content: '您确定要删除吗？',
                    okText: '删 除',
                    cancelText: '取 消',
                    onOk: () => {
                        if (index === 0) {//正面身份证
                            this.fnReset('formData');
                            this.clearCertFrontField();
                            this.clearCertBackField();
                            this.clearAuthField();
                        } else if (index === 1) {//反面身份证
                            this.clearCertBackField();
                            this.clearAuthField();
                            this.partialReset('formData', 'validDate');
                        } else if (index === 2) {//银行卡
                            this.clearBankcardField();
                        }
                    },
                });
            },
            //预览大图
            previewFile (index) {
                let targetObj = this.uploadFile[index];
                this.showPreviewModal = true;
                this.previewUrl = targetObj.bloburl;
            },
            //文件输入框选择后事件
            changeImage (e) {
                let files = e.target.files;
                if (!files || !files.length) return;
                var file = files[0];
                //身份证 限制是3M，营业执照 限制是1M，超过的话调用压缩方法
                // 该大小限制针对base64的字节数算法，base64是源文件大小的4/3，即用文件大小判断的话需要再转一道*0.75
                var maxSize = 1024 * 1024 * 3 * 0.75;
                yx.optim(file, maxSize, (realFile) => {
                    if (realFile.size > maxSize) {
                        return this.errorModal('文件大小超过允许上传的最大值，请重新选择！')
                    }
                    this.loadFile(realFile);
                });
            },
            //清除身份证正面及文件
            clearCertFrontField () {
                this.formData.sfzres = '';
                this.formData.name = '';
                this.formData.certNo = '';
                this.formData.address = '';
                this.formData.race = '';
                this.initUploadFile(0);
            },
            //清除身份证反面及文件
            clearCertBackField () {
                this.formData.sfzres = '';
                this.formData.validDate = '';
                this.formData.validStartDate = '';
                this.formData.validEndDate = '';
                this.initUploadFile(1);
            },
            //清除银行卡号及文件
            clearBankcardField () {
                this.formBankData.bankcardNo = '';
                this.initUploadFile(2);
            },
            //清除银联鉴权页面
            clearAuthField () {
                this.formBankData.phone = '';
                this.clearBankcardField();
            },
            //上传文件到腾讯云
            loadFile (file) {
                let index = this.inputIndex;
                //身份证重新上传的话重置身份数据和银联鉴权数据
                if (index === 0) {
                    this.formData.sfzres = '';
                    this.clearCertFrontField();
                    this.clearCertBackField();
                } else if (index === 1) {
                    this.formData.sfzres = '';
                    this.clearCertBackField();
                }
                //index为2时代表上传银行卡证件
                let targetObj = this.uploadFile[index];
                this.$set(targetObj, 'loading', true);
                this.$set(targetObj, 'pct', 0 + '%');
                var indexOf = file.name.lastIndexOf('.');
                let ext = '';
                if (indexOf > 0) ext = file.name.substring(indexOf + 1).toLowerCase();
                //获取bloburl和hash
                this.Cos.convertBlobInfo(file, (res) => {
                    targetObj.name = res.name;
                    targetObj.bloburl = res.bloburl;
                    targetObj.hash = res.hash;
                    targetObj.hashName = res.hashName;
                    this.todoUpload(file, index);
                })
            },
            //上传腾讯云，使用统一方法
            todoUpload (file, index) {
                const targetObj = this.uploadFile[index];
                if (!targetObj) return false;
                this.Cos.cosUpload({
                    hashName: targetObj.hashName,
                    file: file,
                    onProgress: (progressData) => {
                        const pct = parseInt(progressData.percent * 100) || 0;
                        this.$set(targetObj, 'pct', pct + '%');
                    },
                    complete: (err, data) => {
                        this.$set(targetObj, 'loading', false);
                        if (err) {
                            this.$set(targetObj, 'error', true);
                            return this.errorModal('上传文件到腾讯云出错：' + err.statusCode + ' ' + err.error.Message);
                        }
                        //返回的data为上传结果，有Location,Bucket,Key,ETag,statusCode
                        //更新身份证对应图片的信息
                        this.$set(targetObj, 'size', file.size);
                        this.$set(targetObj, 'name', file.name);
                        this.$set(targetObj, 'remoteKey', data.remoteKey);
                        //通知云盘保存，该页面在离开页面时保存
                        this.Cos.saveItemToYun(targetObj, (saveres) => {
                            if (!saveres || !saveres.items) {
                                this.$set(targetObj, 'error', true);
                                return false;
                            }
                            let item = saveres.items[0];
                            if (item && item.files && item.files[0] && item.files[0].previewUrl) {
                                this.$set(targetObj, 'qcloudUrl', item.files[0].previewUrl);
                                //获取上传地址后请求后台进行识别
                                this.todoOcr(index);
                            } else {
                                this.$set(targetObj, 'error', true);
                            }
                        })
                    }
                })
            },
            //保存附件
            saveItem (index) {
                let targetObj = this.uploadFile[index];
                if (!targetObj) return false;
                const token = this.Cos.token;
                const title = this.Cos.items[targetObj.itemID] ? this.Cos.items[targetObj.itemID].title : "";
                if (!title) return this.errorModal('该附件未在模板中定义')
            },
            //识别结果回填
            setOcrRes (index, ocrRes) {
                if (index === 0) {
                    this.formData.name = ocrRes.name;
                    this.formData.certNo = ocrRes.idcard;
                    this.formData.race = ocrRes.nationCode;
                    this.formData.address = ocrRes.address;
                } else if (index === 1) {
                    this.formData.validDate = ocrRes.validDate;
                    this.formData.validStartDate = ocrRes.validStartDate;
                    let currentTime = new Date().format('yyyy-MM-dd');
                    let currentTimehs = new Date(currentTime);
                    let validEndDatehs = new Date(ocrRes.validEndDate);
                    if ((validEndDatehs.getTime()) - currentTimehs.getTime() < 0) {
                        this.deleteFile(1);
                        return this.errorModal('身份证已失效');
                    }
                    this.formData.validEndDate = ocrRes.validEndDate;
                } else if (index === 2) {
                    this.formBankData.bankcardNo = ocrRes.bankcardNo;
                }
            },
            //请求后台进行图片识别,targetObj为已经上传后更新的图片信息
            todoOcr (index) {
                let isBankcard = index == 2,
                    targetObj = this.uploadFile[index],
                    ocrUrl = isBankcard ? polarisserver + '/credit/scanBankCard' : polarisserver + '/credit/scanIdCard';
                let params = {};
                if (index == 2) {
                    params = {
                        "traceId": this.formData.requestId, //请求跟踪id
                        "transactionId": this.formData.requestId, //请求跟踪id
                        "sysId": "2", //系统标识。app：1 ； pc：2
                        "bankcardUrlStr": targetObj.qcloudUrl, //上传图片的腾讯云地址
                    };
                } else {
                    params = {
                        "traceId": this.formData.requestId, //请求跟踪id
                        "transactionId": this.formData.requestId, //请求跟踪id
                        "sysId": "2", //系统标识。app：1 ； pc：2
                        "cardType": '' + index, //身份证正反面标识 0：人像面 1：国徽面
                        "idcardUrlStr": targetObj.qcloudUrl //targetObj.qcloudUrl, //上传图片的腾讯云地址
                    };
                }
                this.showLoading();
                this.$http.post(ocrUrl, params).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success) {
                        this.$set(targetObj, 'error', true);
                        return this.errorModal(result.errorMessage || '识别失败，请重新上传!');
                    }
                    const ocrRes = result.data; //获取验证结果
                    if (!ocrRes) {
                        this.$set(targetObj, 'error', true);
                        return this.errorModal('识别失败，请重新上传!');
                    }
                    //缺少身份证已校验征信授权
                    this.setOcrRes(index, ocrRes);
                }).catch((err) => {
                    this.hideLoading();
                    this.$set(targetObj, 'error', true);
                    this.errorCatchModal('图片识别请求出错：', err);
                })
            },
            //放弃编辑，返回列表
            cancelEdit () {
                this.$Modal.confirm({
                    title: '提示',
                    closable: false,
                    maskClosable: false,
                    content: '确认放弃此次征信授权？',
                    okText: '确认放弃',
                    cancelText: '继续编辑',
                    onOk: () => {
                        this.$router.back();
                    },
                });
            },
            //删除已上传的身份证图片，需要与腾讯云和云盘交互
            handleRemove (index) {
                this.$Modal.confirm({
                    content: '确认删除此图片？',
                    onOk: () => {
                        this.initUploadFile(index);
                        this.$Message.info('已删除');
                    },
                });
            },
        }
    }
</script>
<style lang="less" scoped>
    .centerdiv {
        margin: 10px 20px 20px 90px;
        width: 500px;
        max-width: 100%;
    }
</style>



